import { ESRI_KEY, GOOGLE_KEY, MAPBOX_TOKEN } from "./constants"

function convertQueryArgs (argDict) {
  // still need ? before, but simplified/safe query argument converter from dictionary to string
  return Object.keys(argDict).reduce((args, key) => {
    args.push(`${key}=${encodeURIComponent(argDict[key])}`)
    return args
  }, []).join('&')
}

export const queryEsri = (query) => {
  const baseUrl = 'https://geocode-api.arcgis.com/arcgis/rest/services/World/GeocodeServer/findAddressCandidates'
  const queryArgs = {
    f: 'pjson',
    singleLine: query,
    maxLocations: 5,
    category: 'Land Features, Parks and Outdoors, Travel and Transport, Water Features',
    outFields: 'PlaceName, Place_Addr, Type',
    searchExtent: '-111.28781740987111, 35.28197585183793, -100.71233650074763, 41.89088257350326', // mostly colorado with some space in surrounding states
    token: ESRI_KEY
  }
  const urlQuery = convertQueryArgs(queryArgs)

  return fetch(`${baseUrl}?${urlQuery}`).then(data => {
    return data.json()
  })
}

export const queryGoogle = (query) => {
  const baseUrl = 'https://maps.googleapis.com/maps/api/geocode/json';
  const queryArgs = {
    address: query,
    key: GOOGLE_KEY,
    bounds: '35.28197585183793,-111.28781740987111|41.89088257350326,-100.71233650074763' // SW and NE corners
  };

  const urlQuery = Object.keys(queryArgs).map(key => {
    return `${key}=${encodeURIComponent(queryArgs[key])}`;
  }).join('&');

  return fetch(`${baseUrl}?${urlQuery}`).then(data => {
    return data.json();
  });
};

export const queryMapbox = (query) => {
  const baseUrl = 'https://api.mapbox.com/geocoding/v5/mapbox.places';
  const queryArgs = {
    access_token: MAPBOX_TOKEN,
    autocomplete: true,
    limit: 5,
    types: 'region,place,locality',
    bbox: '-111.28781740987111,35.28197585183793,-100.71233650074763,41.89088257350326' // xmin, ymin, xmax, ymax
  };

  const urlQuery = Object.keys(queryArgs).map(key => {
    return `${key}=${encodeURIComponent(queryArgs[key])}`;
  }).join('&');

  return fetch(`${baseUrl}/${encodeURIComponent(query)}.json?${urlQuery}`).then(data => {
    return data.json().then(result => {
      result.features = result.features.filter(feat => feat.relevance >= 0.6) // mapbox features were sometimes pretty far off
      return Promise.resolve(result)
    });
  });
};