<template>
  <div class="geocode-control">
    <collapse-card v-model:open="collapseOpen" title="Geocode & Map Controls">
      <div class="checkbox-input">
        <input type="checkbox" :value="grayscale" @input="$emit('update:grayscale', $event)" name="grayscale">
        <label for="grayscale">
          Force Grayscale Maps
          <span class="label-description">
            (this may take a second to setup)
          </span>
        </label>
      </div>
      <div class="geocode-input-container">
        <label for="grayscale">
          Geocode Search Query
          <span class="label-description">
            (will run on all platforms simultaneously)
          </span>
        </label>
        <input v-model="geocodeQuery" placeholder="e.g. Grand Mesa">
        <button @click="clear">Clear</button>
      </div>
      <div class="geocode-comparison">
        <div class="geocode-results-container esri-container">
          <div class="geocode-result-title">ESRI (20k free, then $0.005/request)</div>
          <div class="geocode-results-list">
            <ul>
              <li v-for="(result, index) in esriResults" :key="`esri-${result.address}-${index}`" @click="selectEsriResult(result)">
                <div class="result-name">
                  {{ result.attributes.PlaceName }}
                </div>
                <div class="result-address">
                  {{ result.attributes.Place_Addr }}
                </div>
                <div class="result-coordinates">
                  [{{ result.location.x }}, {{ result.location.y }}]
                </div>
              </li>
              <li v-if="!esriResults.length">
                No results found!
              </li>
            </ul>
          </div>
        </div>
        <div class="geocode-results-container google-container">
          <div class="geocode-result-title">Google (~$0.005/request)</div>
          <div class="geocode-results-list">
            <ul>
              <li v-for="(result, index) in googleResults" :key="`google-${result.place_id}-${index}`" @click="selectGoogleResult(result)">
                <div class="result-name">
                  {{ result.formatted_address }}
                </div>
                <div class="result-coordinates">
                  [{{ result.geometry.location.lng }}, {{ result.geometry.location.lat }}]
                </div>
              </li>
              <li v-if="!googleResults.length">
                No results found!
              </li>
            </ul>
          </div>
        </div>
        <div class="geocode-results-container mapbox-container">
          <div class="geocode-result-title">Mapbox (100k free, then $0.0075 - $0.0045/request)</div>
          <div class="geocode-results-list">
            <ul>
              <li v-for="(result, index) in mapboxResults" :key="`mapbox-${result.id}-${index}`" @click="selectMapboxResult(result)">
                <div class="result-name">
                  {{ result.text }}
                </div>
                <div class="result-address">
                  {{ result.place_name }}
                </div>
                <div class="result-coordinates">
                  [{{ result.geometry.coordinates[0] }}, {{ result.geometry.coordinates[1] }}]
                </div>
              </li>
              <li v-if="!mapboxResults.length">
                No results found!
              </li>
            </ul>
          </div>
        </div>
      </div>
    </collapse-card>
  </div>
</template>

<script>
import { queryEsri, queryGoogle, queryMapbox } from '@/utils/geocoding'
import CollapseCard from './CollapseCard.vue'

export default {
  components: { CollapseCard },
  props: {
    grayscale: Boolean
  },
  data() {
    return {
      searchQuery: '',
      searchTimeout: null,
      collapseOpen: true,
      esriResults: [],
      googleResults: [],
      mapboxResults: []
    }
  },
  methods: {
    clear () {
      this.searchQuery = ''
      this.queryResults()
      this.$emit('clear-markers')
    },
    queryResults () {
      if (this.searchTimeout) {
        clearTimeout(this.searchTimeout)
      }
      this.searchTimeout = setTimeout(() => {
        if (this.searchQuery.length) {
          queryEsri(this.searchQuery).then(data => {
            this.esriResults = data.candidates
          })
          queryGoogle(this.searchQuery).then(data => {
            this.googleResults = data.results
          })
          queryMapbox(this.searchQuery).then(data => {
            this.mapboxResults = data.features
          })

        } else {
          this.esriResults = []
          this.googleResults = []
          this.mapboxResults = []
        }
      }, 500)
    },
    selectEsriResult (result) {
      this.$emit('select-result', [result.location.x, result.location.y])
    },
    selectGoogleResult (result) {
      this.$emit('select-result', [result.geometry.location.lng, result.geometry.location.lat])
    },
    selectMapboxResult (result) {
      this.$emit('select-result', result.geometry.coordinates)
    }
  },
  computed: {
    geocodeQuery: {
      get () {
        return this.searchQuery
      },
      set (val) {
        this.searchQuery = val
        this.queryResults()
      }
    }
  }
}
</script>

