.geocode-control {
  label {
    font-weight: bold;
  }
  label .label-description {
    font-weight: normal;
    font-size: small;
  }
  .checkbox-input {
    margin-bottom: 0.5rem;
  }
  .geocode-input-container {
    margin-bottom: 0.5rem;
    label {
      display: block
    }
    input {
      width: 340px;
      max-width: calc(100% - 52px)
    }
  }

  .geocode-comparison {
    display: flex;
    justify-content: space-between;
    .geocode-results-container {
      .geocode-result-title {
        font-weight: bold;
      }
      .geocode-results-list ul {
        padding-left: 1.5rem;
        li {
          padding: 0.5rem;
          margin: 0.5rem 0;
