<template>
  <div class="collapse-container" :class="{'is-active': open}">
    <div class="collapse-card-title" @click="toggleOpen">
      {{ title }}
      <span class="collapse-icon">
        <chevron-down v-if="open"/>
        <chevron-up v-else/>
      </span>
    </div>
    <div class="collapse-card-content">
      <slot></slot>
    </div>
  </div>
</template>
<script>
import ChevronUp from 'vue-material-design-icons/ChevronUp.vue'
import ChevronDown from 'vue-material-design-icons/ChevronDown.vue'
export default {
  components: { ChevronUp, ChevronDown },
  props: {
    open: Boolean,
    title: String,

  },
  methods: {
    toggleOpen () {
      this.$emit('update:open', !this.open)
    }
  }
}
</script>

<style lang="less">
.collapse-container {
  background-color: white;
  border-radius: 1rem;
  border: 1px solid gainsboro;

  .collapse-card-title {
    font-size: large;
    font-weight: bold;
    border-bottom: 1px solid gainsboro;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    .collapse-icon {
      margin-left: auto;
      position: relative;
      top: 4px;
    }
  }

  .collapse-card-content {
    max-height: 0;
    transition: max-height 300ms padding 300ms;
    overflow: hidden;
    padding: 0;
  }
  &.is-active .collapse-card-content {
    max-height: 1000px;
    padding: 0.5rem 1rem;
  }
}
</style>