export function desaturateExpression (expr) {
  if (Array.isArray(expr)) {
    // console.log(expr)
    // return expr
    return expr.map(desaturateExpression)
  } else if (typeof expr === 'string' && expr.startsWith('#')) {
    return desaturateHex(expr)
  } else if (typeof expr === 'string' && expr.startsWith('hsl')) {
    return desaturateHsl(expr)
  } else if (typeof expr === 'string' && expr.startsWith('rgb')) {
    return desaturateRgb(expr)
  } else {
    return expr
  }
}

function desaturateHex (hexCode) {
  return desaturateHsl(hexToHsl(hexCode))
}

function desaturateHsl (hslCode) {
  const [,saturation, lightness] = hslCode.split(',')
  const lPercent = lightness.includes('%') ? '%' : ''
  const sPercent = saturation.includes('%') ? '%' : ''
  const [h, , l, a] = hslCode.split(',').map(str => Number(str.replace(/[^\d.]/g, '')))
  return a ? `hsla(${h}, 0, ${l}${lPercent}, ${a})`: `hsl(${h}, 0${sPercent}, ${l}${lPercent})`
}

function desaturateRgb (rgbCode) {
  const rgba = rgbCode.match(/(\d+(\.\d+)?)/g).map(Number);
  const [r, g, b, a = 1] = rgba;

  // Convert RGB to HSL
  const hsl = rgbToHsl(r, g, b, a);
  return desaturateHsl(hsl)
}

function rgbToHsl(r, g, b, a = 1) {
  r /= 255;
  g /= 255;
  b /= 255;

  const max = Math.max(r, g, b);
  const min = Math.min(r, g, b);
  let h, s, l = (max + min) / 2;

  if (max === min) {
    h = s = 0; // Achromatic
  } else {
    const d = max - min;
    s = l > 0.5 ? d / (2 - max - min) : d / (max + min);

    switch (max) {
      case r: h = (g - b) / d + (g < b ? 6 : 0); break;
      case g: h = (b - r) / d + 2; break;
      case b: h = (r - g) / d + 4; break;
    }

    h /= 6;
  }

  h = Math.round(h * 360);
  s = Math.round(s * 100);
  l = Math.round(l * 100);

  return `hsla(${h}, ${s}%, ${l}%, ${a})`;
}

function hexToHsl (hex) {
  // Remove the hash if present
  hex = hex.replace(/^#/, '');

  // Parse the hex string
  let bigint;
  if (hex.length === 3) {
    bigint = parseInt(hex.split('').map(c => c + c).join(''), 16);
  } else {
    bigint = parseInt(hex, 16);
  }

  const r = (bigint >> 16) & 255;
  const g = (bigint >> 8) & 255;
  const b = bigint & 255;
  return rgbToHsl(r, g, b)
}

