        v-model:grayscale="forceGrayscale"
      ></geocode-control>
    </div>
    <div class="map-row" :key="`grayscale-${forceGrayscale}-row-1`">
      <map-container v-bind="naturalAtlas" v-on="mapEvents">
        <template #pricing>
          <div class="pricing-model">
            Provided by Natural Atlas
