        </template>
      </map-container>
    </div>
    <div class="map-row" :key="`grayscale-${forceGrayscale}-row-2`">
      <map-container v-bind="mapboxSatellite" v-on="mapEvents">
        <template #pricing>
          <div class="pricing-model">
            <a href="https://www.mapbox.com/pricing#map-loads-for-web">Price / Load:</a>
            <ul>
              <li>free: &lt; 50,000</li>
              <li>$5 / 1000: 50,001 - 100,000</li>
              <li>$4 / 1000: 100,001 - 200,000</li>
              <li>$3 / 1000: 200,001+</li>
            </ul>
          </div>
          <div class="estimated-cost">
            Estimated cost:
            <ul>
              <li>$1200/month ~400k map loads</li>
              <li>$1500/month ~500k map loads</li>
            </ul>
          </div>
        </template>
      </map-container>
      <map-container v-bind="googleTerrain" v-on="mapEvents">
        <template #pricing>
          <div class="pricing-model">
          <a href="https://mapsplatform.google.com/pricing">Price / Load:</a>
            <ul>
              <li>~$7 / 1000</li>
