      </map-container>
    </div>
    <div class="map-row" :key="`grayscale-${forceGrayscale}-row-3`">
      <map-container v-bind="esriOutdoor" v-on="mapEvents">
        <template #pricing>
          <div class="pricing-model">
            Basemaps are included with ArcGIS licenses
          </div>
          <div class="estimated-cost">
            Estimated Cost: (unknown/free)
          </div>
        </template>
      </map-container>
      <map-container v-bind="esriTerrain" v-on="mapEvents">
        <template #pricing>
          <div class="pricing-model">
            Basemaps are included with ArcGIS licenses
          </div>
          <div class="estimated-cost">
            Estimated Cost: (unknown/free)
          </div>
        </template>
      </map-container>
      <map-container v-bind="esriTopo" v-on="mapEvents">
        <template #pricing>
          <div class="pricing-model">
            Basemaps are included with ArcGIS licenses
          </div>
          <div class="estimated-cost">
            Estimated Cost: (unknown/free)
          </div>
        </template>
