  </div>
</template>
<script>
import { MapboxOutdoors, MapboxSatelliteStreets, NaturalAtlas } from '@/style-definitions'
import MapContainer from './MapContainer.vue'
import GeocodeControl from './GeocodeControl.vue'
import { GoogleTerrain } from '@/style-definitions/google'
import { LoadEsriStyles } from '@/style-definitions/esri'
import maplibregl from 'maplibre-gl'
import { desaturateExpression } from '@/utils/color'

export default {
  components: { MapContainer, GeocodeControl },
  data () {
    return {
      isGrayscale: false,
      maps: [],
      mapEvents: {
        'map-load': (evt) => this.onMapLoad(evt),
        'map-move': (evt) => this.onMove(evt)
      },
      naturalAtlas: {
        style: NaturalAtlas,
        title: 'Natural Atlas'
      },
      mapboxOutdoors: {
        title: 'Mapbox Outdoors',
        style: MapboxOutdoors,
      },
      mapboxSatellite: {
        title: 'Mapbox Satellite Streets',
        style: MapboxSatelliteStreets
      },
      googleTerrain: {
        title: 'Google Terrain',
        style: GoogleTerrain
      },
      
      // esri styles have to run through a fetch command and are asynchronous
      // so we'll load these during runtime
      esriOutdoor: null,
      esriTopo: null,
      esriTerrain: null
    }
  },
  created () {
    LoadEsriStyles().then(({EsriOutdoor, EsriTopo, EsriTerrain }) => {
      this.esriOutdoor = {
        title: 'Esri Outdoor',
        style: EsriOutdoor
      }
      this.esriTopo = {
        title: 'Esri Topographic',
        style: EsriTopo
      }
      this.esriTerrain = {
        title: 'Esri Terrain',
        style: EsriTerrain
      }
    })
  },
  methods: {
    onMove({ center, zoom, id }) {
      const moveOtherMap = (map) => {
        map.movingFromOtherDemo = true
        map.setCenter(center)
        map.setZoom(zoom)
        map.movingFromOtherDemo = false;
      }
      // move the other maps not matching the caller's id
      const moveMaps = this.maps.filter(map => map.basemapID !== id)
      moveMaps.forEach(map => moveOtherMap(map))
    },
    onMapLoad (map) {
      this.maps.push(map);
      const style = map.getStyle();
      
      if (this.forceGrayscale) {
        style.layers.forEach(layer => {
          if (layer.type === 'raster') {
            layer.paint = Object.assign({}, layer.paint, { 'raster-saturation': -1 })
          }
          if (layer.paint) {
            for (const [key, value] of Object.entries(layer.paint)) {
              if (key.includes('color')) {
                layer.paint[key] = desaturateExpression(value)
              }
            }
          }
          map.setStyle(style);
        });
      }
    },
    clearMarkers () {
      console.log('clear markers called?')
      this.maps.forEach(map => {
        // Remove existing marker if it exists
        if (map._geocodeMarker) {
          map._geocodeMarker.remove();
          map._geocodeMarker = null;
        }
      })
    },
    placeMarkers (coordinates) {
      this.maps.forEach(map => {
        // Remove existing marker if it exists
        if (map._geocodeMarker) {
          map._geocodeMarker.remove();
          map._geocodeMarker = null;
        }

        // Create a new marker
        const marker = new maplibregl.Marker()
          .setLngLat(coordinates)
          .addTo(map);

        // Store the new marker in the map object
        map._geocodeMarker = marker;
      });
    }
  },
  computed: {
    forceGrayscale: {
      get () {
        return this.isGrayscale
      },
      set (val) {
        if (this.isGrayscale != val) {
          this.maps = []; // clear out maps on change of this value, they will all re-render
          this.isGrayscale = val
        }
      }
    }
  }
}
</script>
