<template>
  <div class="basemap-container" :id="basemapID"></div>
</template>

<script>
import { INIT_ZONE_BOUNDS, MAPBOX_TOKEN, ZONE_BOUNDS } from '@/utils/constants'
import maplibregl from 'maplibre-gl'

export default {
  props: {
    style: {
      type: Object, // mapbox style definition,
      required: true
    },
    sources: Array, // not required array of sources to call addSource(source)
    layers: Array, // not required array of layers to call addLayer(layer)
  },
  data () {
    return {
      basemapID: '',
      map: null,
    }
  },
  created () {
    // create a semi-random basemap id using a hexidecimal random number
    this.basemapID = `basemap-${Math.random().toString(16).slice(2)}`
  },
  mounted () {
    if (this.map) {
      // this fixes a dev hot-reload issue where component is remounted
      this.map.remove()
      this.map = null;
    }
    // this is concerning, but for some reason the mapbox strings are getting converted to objects?
    // with form { mapbox: '//rest-of-url'}
    // is this somehow running through an eval or something?
    const style = this.style.mapbox ? `mapbox:${this.style.mapbox}` : this.style
    this.map = new maplibregl.Map({
      accessToken: MAPBOX_TOKEN,
      container: this.basemapID,
      style,
      // style: this.style,
      maxZoom: 14,
      bounds: INIT_ZONE_BOUNDS, // initial
      maxBounds: ZONE_BOUNDS // maximum
    })
    this.map.basemapID = this.basemapID // set this so parents will have an easier time figuring things out

    this.map.addControl(new maplibregl.NavigationControl({ showCompass: false }), 'top-left')
    this.map.dragRotate.disable()
    this.map.touchZoomRotate.disableRotation()
    this.map.on('load', () => {
      if (this.sources?.length) {
        this.sources.forEach(source => {
          this.map.addSource(source.name, source)
        })
      }
      if (this.layers?.length) {
        this.layers.forEach(layer => {
          this.map.addLayer(layer)
        })
      }
      this.$emit('map-load', this.map)
      // make other maps follow this map's movement
      // consider using moveend if performance sucks
      this.map.on('move', () => {
        const bounds = this.map.getBounds();
        const center = this.map.getCenter();
        const zoom = this.map.getZoom();
        if (!this.map.movingFromOtherDemo) {
          this.$emit('map-move', { center, bounds, zoom, id: this.basemapID });
        }
      })
    })

  },
  methods: {

  }
}
  
</script>

<style lang="less">
.basemap-container {
  width: 100%;
  height: 100%;
}
</style>