/* eslint-disable no-async-promise-executor */
import { GOOGLE_KEY } from "@/utils/constants";
import maplibregl from "maplibre-gl";
const sessions = {};

// copied and modified from: https://github.com/traccar/maplibre-google-maps/blob/main/index.js
// I think they used a different version of maplibre which changed how addProtocol behaved
// eslint-disable-next-line no-unused-vars
export function googleProtocol(params, callback) {
  const url = new URL(params.url.replace('google://', 'https://'));
  const sessionKey = `${url.hostname}?${url.searchParams}`;
  const key = url.searchParams.get('key');

  let value = sessions[sessionKey];
  if (!value) {
    value = new Promise(async (resolve) => {
      const mapType = url.hostname;
      const layerType = url.searchParams.get('layerType');
      const overlay = url.searchParams.get('overlay');

      const sessionRequest = {
        mapType,
        language: "en-US",
        region: "US",
        scale: "scaleFactor2x",
        highDpi: true,
      };
      if (layerType) {
        sessionRequest.layerTypes = [layerType];
      }
      if (overlay) {
        sessionRequest.overlay = overlay === 'true';
      }

      const response = await fetch(`https://tile.googleapis.com/v1/createSession?key=${key}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(sessionRequest),
      });
      const result = await response.json();
      sessions[sessionKey] = result.session;
      resolve();
    });
    sessions[sessionKey] = value;
    value;
  }

  const loadData = (session) => {
    fetch(`https://tile.googleapis.com/v1/2dtiles${url.pathname}?session=${session}&key=${key}`).then(tile => {
      tile.arrayBuffer().then(data => {
        callback(null, data, null, null);
      })
    }).catch(error => {
      callback(error)
    });
  }

  if (value instanceof Promise) {
    // first call this is a promise to fetch session data
    value.then(() => {
      loadData(sessions[sessionKey])
    })
  } else {
    // subsequent calls this is a string for the session key
    loadData(sessions[sessionKey])

  }

  return { cancel: () => {} }
}

export function createGoogleStyle(id, mapType, key) {
  const style = {
    "version": 8,
    "sources": {
    },
    "layers": [
      {
        "id": id,
        "type": "raster",
        "source": id,
      },
    ],
  };
  let tile = `google://${mapType}/{z}/{x}/{y}?key=${key}`
  if (mapType === 'terrain') {
    tile += '&layerType=layerRoadmap'
  }
  style.sources[id] = {
    "type": "raster",
    "tiles": [tile],
    "tileSize": 512,
    "attribution": "&copy; Google Maps",
    "maxzoom": 19,
  };
  return style;
}

maplibregl.addProtocol('google', googleProtocol)

export const GoogleSatellite = createGoogleStyle('google-satellite', 'satellite', GOOGLE_KEY)
export const GoogleTerrain = createGoogleStyle('google-terrain', 'terrain', GOOGLE_KEY)
