import { ESRI_KEY } from "@/utils/constants"

const makeEsriStyle = (basemapPart) => {
  return fetch(`https://basemapstyles-api.arcgis.com/arcgis/rest/services/styles/v2/styles/${basemapPart}?token=${ESRI_KEY}`).then(response => {
    return response.json()
  })
}

// declare these and conditionally set them
// this way we only load them once
let EsriOutdoor
let EsriTopo
let EsriTerrain

const LoadEsriStyles = async () => {
  if (!EsriOutdoor || !EsriTopo || !EsriTerrain) {
    const [
      outdoor,
      topo,
      terrain
    ] = await Promise.all([
      makeEsriStyle('arcgis/outdoor'),
      makeEsriStyle('arcgis/topographic'),
      makeEsriStyle('arcgis/terrain')
    ])  
    EsriOutdoor = outdoor
    EsriTopo = topo
    EsriTerrain = terrain
  }

  return {
    EsriOutdoor,
    EsriTopo,
    EsriTerrain
  }
}

export { LoadEsriStyles }
export default LoadEsriStyles

